
import './globals.css'
import { ThemeProvider } from '@/components/theme-provider'
import { ModeToggle } from '@/components/toggle'
import { Header } from '@/components/Header' // ðŸ‘ˆ Importa o novo Header

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="pt-BR">
      <body className="flex flex-col min-h-screen">
        
        <Header /> {/* Usa o Header aqui */}

        <main className="max-w-7xl mx-80 px-4 sm:px-6 md:px-8 py-6 sm:py-8">
          <ThemeProvider
            attribute="class"
            defaultTheme="system"
            enableSystem
            disableTransitionOnChange
          >
            <ModeToggle />
            {children}
          </ThemeProvider>
        </main>

        <footer className="bg-gradient-to-r from-green-500 to-green-600 text-white text-center py-4">
          <p>Arthur Thomas Mariano de Souza - RM: 561061</p>
          <p>Jhonatta Lima Sandes - RM: 560277</p>
        </footer>
      </body>
    </html>
  )
}
